@use "base/mixins" as m;
@use "base/variables" as v;
//Codigo para informacio del festival
.lineup {
  background-color: v.$rosa;
  padding: 5rem 0;
  h3,
  p {
    text-align: center;
    color: v.$blanco;
  }
  .dia {
    text-transform: uppercase;
    font-weight: 700;
    margin: 5rem 0;
  }
}
.escenario__contenedor {
  @include m.tablet {
    @include m.grid(2, 5rem);
    //text-align: center;
  }
  @include m.ipad-tablet{
    @include m.grid(2, 2rem);
    .bg-amarillo,
    .bg-verde{
     width: 90%;
    }
  }
  @include m.tablet{
    @include m.grid(2, 2rem);
    .bg-amarillo,
    .bg-verde{
     width: 95%;
    }
  }
  p {
    color: v.$negro;
  }
  .bg-amarillo {
    background-color: v.$amarillo;
    border-radius: 2%;
    margin-bottom: 2rem;
    li:nth-child(odd) {
      background-color: darken(v.$amarillo, 15%);
    }
  }
  .bg-verde {
    background-color: v.$verde;
    border-radius: 2%;
    margin-bottom: 2rem;
    li:nth-child(odd) {
      background-color: darken(v.$verde, 10%);
    }
  }
  .escenario {
    padding-top: 2rem;
    p {
      &:after {
        content: "";
        width: 20rem;
        height: 10rem;
        display: block;
        background-size: 15rem;
        background-position: center center;
        background-repeat: no-repeat;
        margin: 2rem auto 0 auto;
      }
    }
  }
}
.calendario {
  @include m.resetear_lista;
  li {
    padding: 2.5rem;
    font-weight: 700;
    span {
      font-weight: 400;
    }
  }
}
.edm .nombre__escenario:after {
  background-image: url("../img/icono_edm.png");
}
.rock .nombre__escenario:after {
  background-image: url("../img/icono_rock.png");
}
