html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:after,
*:before {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.bodyScroll {
  padding-top: 8.44rem;
}

p {
  color: #000000;
  font-size: 2rem;
  line-height: 1.5;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  border-radius: 2%;
  -webkit-box-shadow: 0px 1px 5px 0px rgb(0, 0, 0);
  -moz-box-shadow: 0px 1px 5px 0px rgb(0, 0, 0);
  box-shadow: 0px 1px 5px 0px rgb(0, 0, 0);
}

.header {
  left: 0;
  top: 0;
  background-color: #4cbbb3;
}
@media (min-width: 768px) {
  .header.fijar__barra {
    position: fixed;
    width: 100%;
    box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.75);
  }
}
@media (min-width: 768px) {
  .header .contenido__header {
    display: flex;
    padding: 1rem;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
  margin-bottom: 1rem;
}
.header h1:hover {
  color: #752f97;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}

@media (min-width: 768px) {
  .navegacion__principal {
    display: flex;
    text-align: center;
    align-items: center;
    column-gap: 2rem;
  }
}
.navegacion__principal a {
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  color: #ffffff;
  font-size: 2.4rem;
  margin-bottom: 0;
  row-gap: 3rem;
}
.navegacion__principal a:hover {
  color: #752f97;
}
@media (min-width: 768px) {
  .navegacion__principal a {
    margin: 0;
  }
}
@media (min-width: 480px) {
  .navegacion__principal a {
    margin: 0;
  }
}

.video {
  position: relative;
  max-height: 80%;
  overflow: hidden;
  margin: 0;
}
@media (min-width: 768px) {
  .video {
    width: 100%;
  }
}
.video .overlay {
  background: rgb(2, 0, 36);
  background: linear-gradient(341deg, rgb(2, 0, 36) 0%, rgba(90, 43, 126, 0.4864320728) 55%);
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  max-width: 100%;
  min-width: 50%;
  display: inline-block;
}
@media (min-width: 1200px) {
  .video video {
    width: 100%;
  }
}

.contenido__video {
  z-index: 1;
}
.contenido__video h1 {
  color: #ffffff;
  margin: 0;
}
@media (min-width: 300px) {
  .contenido__video h1 {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  .contenido__video h1 {
    font-size: 7rem;
  }
}
.contenido__video p {
  background-color: #f53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 2.4rem;
}
@media (min-width: 300px) {
  .contenido__video p {
    font-size: 1.4rem;
  }
}
@media (min-width: 768px) {
  .contenido__video p {
    font-size: 2.4rem;
  }
}

.contenedor--lineup {
  margin-left: 2rem;
  margin-right: 2rem;
}
@media (min-width: 768px) {
  .contenedor--lineup {
    margin-left: 5rem;
    margin-right: 3rem;
  }
}

.sobre__festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre__festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido__festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido__festival {
    margin-top: 0;
  }
}
.contenido__festival h2 {
  margin-bottom: 0;
}
.contenido__festival .fecha {
  color: #f53756;
  font-weight: 700;
  border-bottom: 1px solid #f53756;
}
.contenido__festival p {
  margin-bottom: 1rem;
}
.contenido__festival .linea {
  width: 100%;
  margin-top: 0;
  color: #f53756;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .dia {
  text-transform: uppercase;
  font-weight: 700;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenario__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
@media (min-width: 810px) {
  .escenario__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .escenario__contenedor .bg-amarillo,
  .escenario__contenedor .bg-verde {
    width: 90%;
  }
}
@media (min-width: 768px) {
  .escenario__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .escenario__contenedor .bg-amarillo,
  .escenario__contenedor .bg-verde {
    width: 95%;
  }
}
.escenario__contenedor p {
  color: #000000;
}
.escenario__contenedor .bg-amarillo {
  background-color: #fef46d;
  border-radius: 2%;
  margin-bottom: 2rem;
}
.escenario__contenedor .bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}
.escenario__contenedor .bg-verde {
  background-color: #4cbbb3;
  border-radius: 2%;
  margin-bottom: 2rem;
}
.escenario__contenedor .bg-verde li:nth-child(odd) {
  background-color: #3a9a93;
}
.escenario__contenedor .escenario {
  padding-top: 2rem;
}
.escenario__contenedor .escenario p:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  background-size: 15rem;
  background-position: center center;
  background-repeat: no-repeat;
  margin: 2rem auto 0 auto;
}

.calendario {
  list-style: none;
  margin: 0;
  padding: 0;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.edm .nombre__escenario:after {
  background-image: url("../img/icono_edm.png");
}

.rock .nombre__escenario:after {
  background-image: url("../img/icono_rock.png");
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria__imagenes {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria__imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria__imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 810px) {
  .galeria__imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .galeria__imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.galeria__imagenes:hover {
  cursor: pointer;
  filter: brightness(150%);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  background-color: #ffffff;
  color: #000000;
  border-radius: 50%;
  font-weight: 700;
  display: grid;
  place-items: center;
}
.btn-cerrar:hover {
  background-color: #f53756;
  color: #ffffff;
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
  background-color: #4cbbb3;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor__boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase .nombre__pase {
  text-align: center;
  color: #ffffff;
  font-size: 2.8rem;
}
.pase__heder, .pase__footer {
  padding: 2rem;
}
.pase__heder p, .pase__footer p {
  color: #ffffff;
  text-align: center;
  margin: 0;
}
.pase__heder {
  background-color: #f53756;
  text-transform: uppercase;
}
.pase__heder p {
  font-size: 1.6rem;
}
.pase__body {
  background-color: #ffffff;
}
.pase__body ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pase__body ul li,
.pase__body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase__footer {
  background-color: #752f97;
  color: #ffffff;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor__footer {
  background-color: #f53756;
  padding: 2rem;
}
.contenedor__footer p {
  text-align: center;
  color: #ffffff;
  font-weight: 700;
}