//Hoja de estilos para el header
@use "base/variables" as v;
@use "base/mixins" as m;
.header {
  left: 0;
  top: 0;
  @include m.tablet {
    &.fijar__barra {
      position: fixed;
      width: 100%;
      box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.75);
    }
  }
  
  .contenido__header {
    @include m.tablet {
      display: flex;
      padding: 1rem;
      justify-content: space-between;
      align-items: center;
    }
  }
  background-color: v.$verde;
  h1 {
    color: v.$blanco;
    text-align: center;
    margin-bottom: 1rem;

    &:hover {
      color: v.$morado;
    }
    @include m.tablet {
      margin: 0;
    }
  }
}
.navegacion__principal {
  @include m.tablet {
    display: flex;
    text-align: center;
    align-items: center;
    column-gap: 2rem;
  }

  a {
    display: block;
    margin-bottom: 1rem;
    text-align: center;
    color: v.$blanco;
    font-size: 2.4rem;
    margin-bottom: 0;
    row-gap: 3rem;
    &:hover {
      color: v.$morado;
    }
    @include m.tablet {
      margin: 0;
    }
    @include m.telefono {
      margin: 0;
    }
  }
}
