@use 'base/mixins' as m;
@use 'base/variables' as v;
.boletos {
  padding: 5rem 0;
  background-color: v.$verde;
  h3 {
    text-align: center;
    color: v.$blanco;
    margin-bottom: 2rem;
  }
  .contenedor__boletos {
    //text-align: center;
    @include m.tablet {
      @include m.grid(2, 5rem);
    }
  }
}
.pase {
  .nombre__pase {
    text-align: center;
    color: v.$blanco;
    font-size: 2.8rem;
  }
  &__heder,
  &__footer {
    padding: 2rem;
    p {
      color: v.$blanco;
      text-align: center;
      margin: 0;
    }
  }
  &__heder {
    background-color: v.$rosa;
    text-transform: uppercase;
    p {
      font-size: 1.6rem;
    }
  }
  &__body {
    background-color: v.$blanco;
    ul {
      @include m.resetear_lista;
      li,
      :nth-child(odd) {
        //agrega una linea entre elementos de la lista
        background-color: v.$blanco;
        padding: 1rem;
        border-bottom: 1px solid #e1e1e1;
      }
    }
  }
  &__footer {
    background-color: v.$morado;
    color: v.$blanco;
    font-weight: 700;
    font-size: 2.4rem;
  }
}
