//Estilos para el video
@use "base/variables" as v;
@use "base/mixins" as m;

.video {
  position: relative;
  max-height: 80%;
  overflow: hidden;
  margin: 0;
  @include m.tablet {
    width: 100%;
  }

  .overlay {
    background: rgb(2, 0, 36);
    background: linear-gradient(
      341deg,
      rgba(2, 0, 36, 1) 0%,
      rgba(90, 43, 126, 0.4864320728291317) 55%
    );
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  video {
    max-width: 100%;
    min-width: 50%;
    display: inline-block;

    @include m.destok {
      width: 100%;
    }
  }
}
.contenido__video {
  z-index: 1;
  h1 {
    
    color: v.$blanco;
    margin: 0;
    
    @include m.menorResolucion {
      font-size: 3rem;
   
    }
    @include m.tablet {
      font-size: 7rem;
    }
  }
  p {
    background-color: v.$rosa;
    display: inline-block;
    padding: 1rem 2rem;
    color: v.$blanco;
    font-weight: 700;
    font-size: 2.4rem;
    @include m.menorResolucion {
      font-size: 1.4rem;
    }
   @include m.tablet {
    font-size: 2.4rem;
    
   }
  }
}
