//Estilos globales del documento
@use "variables" as v;
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
body {
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
  &.fijar-body{
    overflow: hidden;//anula el scroll
  }
  &.bodyScroll{
  padding-top: 8.44rem;
  }
}
p {
  color: v.$negro;
  font-size: 2rem;
  line-height: 1.5;
}
.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}
a {
  text-decoration: none;
}
h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}
h1 {
  font-size: 4rem;
}
h2 {
  font-size: 3.6rem;
}
h3 {
  font-size: 3rem;
}
img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  border-radius: 2%;
  -webkit-box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 1);
  -moz-box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 1);
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 1);
}
