//estilos del contenido festival
@use "base/mixins" as m;
@use "base/variables" as v;
.contenedor--lineup {
  margin-left: 2rem;
  margin-right: 2rem;
  @include m.tablet {
    margin-left: 5rem;
    margin-right: 3rem;
  }
}
.sobre__festival {
  padding: 5rem 0;
  @include m.tablet {
    @include m.grid(2, 5rem);
  }
}
.contenido__festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
  @include m.tablet {
    margin-top: 0;
  }

  h2 {
    margin-bottom: 0;
  }
  .fecha {
    color: v.$rosa;
    font-weight: 700;
    border-bottom: 1px solid v.$rosa;
  }
  p {
    margin-bottom: 1rem;
  }
  .linea {
    width: 100%;
    margin-top: 0;
    color: v.$rosa;
  }
}
