@use "base/mixins" as m;
@use "base/variables" as v;
.galeria {
  padding: 5rem 0;
  h3 {
    text-align: center;
  }
}
.galeria__imagenes {
  @include m.resetear_lista;
  @include m.grid(2, 2rem);
  @include m.telefono {
    @include m.grid(3, 2rem);
  }
  @include m.tablet {
    @include m.grid(4, 2rem);
  }
  @include m.ipad-tablet {
    @include m.grid(4, 2rem);
  }
  @include m.destok {
    @include m.grid(6, 2rem);
  }
  &:hover{
    cursor: pointer;
    filter: brightness(150%);
  }
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;

  display: grid;
  place-items: center;
  background-color: rgb(0 0 0 /0.5);
  picture {
    display: block;
    max-width: 80rem;
  }
}
.btn-cerrar {
  width: 4rem;
  height: 4rem;
  background-color: v.$blanco;
  color: v.$negro;
  border-radius: 50%;
  font-weight: 700;
  display: grid;
  place-items: center;
  &:hover {
    background-color: v.$rosa;
    color: v.$blanco;
    cursor: pointer;
  }
}
