@mixin telefono {
  @media (min-width: v.$telefono) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}
@mixin destokXL {
  @media (min-width: v.$desktop) {
    @content;
  }
}
@mixin ipad-tablet {
  @media (min-width: v.$ipad-tablet) {
    @content;
  }
}

@mixin destok {
  @media (min-width: v.$desktopXL) {
    @content;
  }
}
@mixin menorResolucion {
  @media (min-width: v.$menor_resolucion) {
    @content;
  }
}
@mixin grid($columnas,$espaciado){
  display: grid;
  grid-template-columns: repeat($columnas,1fr);
  gap: $espaciado;
}
@mixin resetear_lista{
  list-style: none;
  margin: 0;
  padding: 0;
}